export default {
    data () {
        return {
            show:true,
            num:1,
            isActive:false,
            isDark:false,
            isError:false
            
        }
    },
    methods : {
        back () {
            this.$router.back(-1);
        },
        // popup弹出层
        showPopup () {
            this.show=true;
        },
        toMinis () {
           this.num==0?this.num=0:this.num--;           
        },
        toAdd () {
           this.num++; 
        },
        toRecharge () {
            console.log(this.num);
            this.isActive=true;
            this.isError=true;
        },
        toClose () {
            this.isError=false;
        }
    },
    watch : {
        num : function () {
            this.num==0?this.isDark=true:this.isDark=false;
        }
    }
   
}



